*, :before, :after {
  box-sizing: border-box;
  gap: 1rem;
  margin: 0;
  padding: 0;
}

body {
  flex-direction: column;
  align-items: center;
  margin: 1rem;
  display: flex;
}

header, #game-container, #result-display {
  flex-direction: column;
  align-items: center;
  display: flex;
}

#game-container {
  width: 100%;
  max-width: 600px;
  min-height: 500px;
}

#game-stats {
  flex-direction: row;
  display: flex;
}

.stats {
  padding: 1rem;
}

#computer-stats {
  text-align: right;
}

.hidden {
  display: none !important;
}

.bar-wrapper {
  border: 1px solid #000;
  width: 100px;
  height: 20px;
  position: relative;
  overflow: hidden;
}

.bar {
  background-color: green;
  height: 100%;
  transition: width .3s;
  display: block;
  position: relative;
  overflow: hidden;
}

.bar-text {
  pointer-events: none;
  white-space: nowrap;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.bar.full {
  background-color: #4caf50;
  width: 100%;
}

.bar.half {
  background-color: #ff9800;
  width: 50%;
}

.bar.zero {
  background-color: #f44336;
  width: 5%;
}
